.App {
  overflow-x: hidden;
  text-align: center;
  padding-left: 0%;
  padding-right: 0%;
}

.container {
  text-align: left;
}

.bg-darkgreen {
  background-color: #006400;
}

.bg-lightgreen {
  background-color: #38b000;
}

.link {
  text-decoration: none;
}

.custom-sep {
  border: 1px solid;
  border-color: white;
}

.btn-outline-color5{color:#38b000;border-color:#38b000}
.btn-outline-color5:hover{color:#fff;background-color:#38b000;border-color:#38b000}
.btn-check:focus+.btn-outline-color5,.btn-outline-color5:focus{box-shadow:0 0 0 .25rem rgba(25,135,84,.5)}
.btn-check:active+.btn-outline-color5,.btn-check:checked+.btn-outline-color5,.btn-outline-color5.active,.btn-outline-color5.dropdown-toggle.show,.btn-outline-color5:active{color:#fff;background-color:#38b000;border-color:#38b000}
.btn-check:active+.btn-outline-color5:focus,.btn-check:checked+.btn-outline-color5:focus,.btn-outline-color5.active:focus,.btn-outline-color5.dropdown-toggle.show:focus,.btn-outline-color5:active:focus{box-shadow:0 0 0 .25rem rgba(25,135,84,.5)}
.btn-outline-color5.disabled,.btn-outline-color5:disabled{color:#38b000;background-color:transparent}

